'use client'

import { Megaphone } from 'lucide-react'

export default function AnnouncementBar() {
  return (
    <div className="
      bg-gradient-to-r from-red-600 to-red-700 
      text-white py-2 px-4 text-center text-sm sm:text-base 
      flex items-center justify-center gap-2 
      shadow-md
      animate-[fadeIn_0.6s_ease-in-out]
    ">
      <Megaphone className="w-4 h-4 text-white animate-pulse" />

      <p className="font-semibold tracking-wide drop-shadow-sm">
        Bem-vindo(a) às Lojas Saroma 
      </p>
    </div>
  )
}
